const SearchInput = () => {
  return (
    <div className="flex items-center -space-x-1 h-9">
      <input
        type="text"
        placeholder="Search anything"
        className="text-sm outline-none ring-inset text-black ring-2 ring-orange-400 rounded-md  px-5  h-full"
      />
      <div className="bg-orange-600 text-white h-full flex items-center  px-2 rounded-r-md">
        <svg
          className="w-6 h-6"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
          />
        </svg>
      </div>
    </div>
  );
};

export default SearchInput;
