import { CSSProperties, MouseEventHandler } from "react";
import { Link, useLocation } from "react-router-dom";

export interface BottomNavItemProps {
  onClick?: MouseEventHandler;
  url?: string;
  label: string;
  labelStyle?: CSSProperties;
  labelClassName?: string;
  navClassName?: string;
  navStyle?: CSSProperties;
  icon: JSX.Element;
  type: "link" | "button";
}

const BottomNavItem = ({
  icon,
  label,
  onClick,
  url,
  labelStyle,
  labelClassName,
  navClassName,
  navStyle,
  type,
}: BottomNavItemProps) => {
  const { pathname } = useLocation();

  const active = pathname === url;

  if (type === "link") {
    return (
      <Link
        to={url ?? "#"}
        className={` flex flex-col py-1 px-2 select-none focus:outline-none hover:text-orange-600 hover:bg-white rounded-md items-center text-xs ${
          active && "bg-white text-orange-600"
        } ${navClassName}`}
        style={navStyle}
      >
        {icon}
        <span className={labelClassName} style={labelStyle}>
          {label}
        </span>
      </Link>
    );
  }

  return (
    <button
      className={`flex flex-col items-center text-xs py-1 px-2 focus:outline-none border-white hover:text-orange-600 hover:bg-white rounded-md  select-none  ${
        active && "bg-white text-orange-600"
      } ${navClassName}`}
      onClick={onClick}
      style={navStyle}
    >
      {icon}
      <span className={labelClassName} style={labelStyle}>
        {label}
      </span>
    </button>
  );
};

export default BottomNavItem;
