import { useState } from "react";
import { Link } from "react-router-dom";
import SearchInput from "../SearchInput";
import BottomNavItem from "./BottomNavItem";
import NavItem, { NavItemProps } from "./NavItem";

export const navItems: NavItemProps[] = [
  { label: "Shop", url: "/" },
  { label: "Dashbaord", url: "/dashboard" },
  { label: "Login", url: "/login" },
];

const IndexNavbar = () => {
  const [navOpen, setNavOpen] = useState(false);

  const toggleNav = () => setNavOpen(!navOpen);
  return (
    <div className=" font-poppins text-white fixed w-full z-30 inset-x-0 top-0">
      {/* MOBILE TOP MENU */}

      <div className=" md:hidden flex justify-between px-5 items-center py-3 sticky inset-x-0 top-0 bg-orange-50 text-orange-600 border-b-2 border-orange-700  ">
        <Link to="/" className="flex items-center space-x-2 font-bold">
          <svg
            className="w-6 h-6"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4"
            />
          </svg>
          <span>Tuagye</span>
        </Link>
        <div className=" hidden sm:block">
          <SearchInput />
        </div>
        <Link
          to="/ads/create"
          target="_blank"
          className="flex  items-center p-1 shadow-xl border-none ring-1 ring-orange-500 rounded-md hover:bg-orange-600 hover:text-white focus:shadow-none "
        >
          <svg
            className="w-6 h-6 animate-pulse "
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 6v6m0 0v6m0-6h6m-6 0H6"
            />
          </svg>

          <span className="text-sm pr-1 ">Create Ad</span>
        </Link>
      </div>

      {/* MOBILE BOTTOM-NAVIGATION */}

      <div className=" md:hidden flex justify-around items-center py-1 fixed inset-x-0 bottom-0 bg-orange-600   shadow-lg border-b-2 border-gray-50 border-opacity-5 ">
        <BottomNavItem
          type="link"
          url="/"
          label="Home"
          icon={
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6"
              />
            </svg>
          }
        />
        <BottomNavItem
          type="link"
          label="Products"
          url="/ads"
          icon={
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10"
              />
            </svg>
          }
        />
        <BottomNavItem
          label="Categories"
          type="button"
          onClick={toggleNav}
          icon={
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2V6zM14 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2V6zM4 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2v-2zM14 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2v-2z"
              />
            </svg>
          }
        />
        <BottomNavItem
          label="Dashboard"
          type="link"
          url="/dashboard"
          icon={
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z"
              />
            </svg>
          }
        />
      </div>

      {/* DESKTOP MENU */}

      <nav className="hidden  md:flex justify-between px-8   sticky inset-x-0 top-10 items-center font-bold   py-4 bg-white text-orange-600  border-b-2 border-orange-400 border-opacity-75 ">
        <NavItem
          label={
            <div className="flex items-center space-x-6">
              <svg
                className="w-6 h-6"
                fill="currentColor"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M5 4a2 2 0 012-2h6a2 2 0 012 2v14l-5-2.5L5 18V4z" />
              </svg>
              <span>Tuagye</span>
            </div>
          }
          url="/"
        />
        <ul className=" items-center hidden space-x-8 md:flex">
          <li>
            <Link
              to="/"
              aria-label="Our product"
              title="Our product"
              className="font-medium tracking-wide text-gray-700 transition-colors duration-200 hover:text-orange-500"
            >
              Shop
            </Link>
          </li>
          <li className="hidden lg:block">
            <Link
              to="/dashboard"
              aria-label="Our product"
              title="Our product"
              className="font-medium tracking-wide text-gray-700 transition-colors duration-200 hover:text-orange-500"
            >
              Dashboard
            </Link>
          </li>
          <SearchInput />
        </ul>
        <ul className=" items-center hidden md:flex md:space-x-8 ">
          <li className="hidden lg:block">
            <Link
              to="/login"
              className="inline-flex items-center justify-center h-8 px-4 font-medium tracking-wide text-orange-500 ring-2 ring-orange-400 border-orange-600 transition duration-200 rounded shadow-md hover:bg-orange-600 hover:text-white hover:ring-orange-200 focus:ring-offset-2 focus:ring-orange-600"
              aria-label="Log In"
              title="Log In"
            >
              Log In
            </Link>
          </li>
          <li>
            <Link
              to="/ads/create"
              className="inline-flex items-center justify-center h-8 px-4 font-medium tracking-wide bg-orange-600 text-white ring-2 ring-orange-400 border-orange-600 transition duration-200 rounded shadow-md hover:bg-white hover:text-orange-600  focus:ring-offset-2 focus:ring-orange-600 focus:bg-orange-600 focus:text-white "
              aria-label="Crete Advet"
              title="Crete Advet"
            >
              Create Ad
            </Link>
          </li>
        </ul>
      </nav>

      {/* MOGBILE SID SLIDING MENU */}
      {/* SIDEBAR OVERLAY */}
      <div
        onClick={toggleNav}
        className={` ${
          navOpen ? "block" : "hidden"
        } cursor-pointer fixed z-20 inset-0 bg-orange-200 opacity-70 transition-opacity md:hidden`}
      ></div>
      {/* END OF SIDBAR OVERLAY */}
      <div
        id="mobileMenu"
        className={`${navOpen ? "translate-x-0" : "-translate-x-full"}
        transform-gpu fixed z-30 inset-y-0 left-0 w-64 transition duration-300  
        -translate-y-0 md:hidden flex flex-col font-poppins items-start px-5 font-bold text-xl bg-gray-900 py-5 space-y-6 `}
        onClick={toggleNav}
      >
        <div className="flex items-center space-x-7 mb-5">
          <svg
            className="w-6 h-6"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M5 4a2 2 0 012-2h6a2 2 0 012 2v14l-5-2.5L5 18V4z" />
          </svg>
          <h3 className="font-Indulta font-medium text-3xl">Tuagye</h3>
          <svg
            onClick={toggleNav}
            className="w-6 h-6 cursor-pointer"
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
              clipRule="evenodd"
            />
          </svg>
        </div>
        {navItems.map(({ label, url }, i) => (
          <NavItem
            key={i}
            label={label}
            url={url}
            navClassName="w-full flex item-start hover:bg-warmGray-100 hover:text-black rounded-md py-2 px-5"
          />
        ))}
      </div>
    </div>
  );
};

export default IndexNavbar;
