import { CSSProperties } from "react";
import { Link } from "react-router-dom";

export interface NavItemProps {
  label: any;
  url: string;
  labelClassName?: string;
  navClassName?: string;
  navStyle?: CSSProperties;
  labelStyle?: CSSProperties;
}

const NavItem = ({
  label,
  url,
  labelClassName,
  navClassName,
  navStyle,
  labelStyle,
}: NavItemProps) => {
  return (
    <Link
      className={`font-medium tracking-wide text-gray-700 transition-colors duration-200 hover:text-orange-500 text-xl ${navClassName}`}
      style={navStyle}
      to={url}
      aria-label={label}
      title={label}
    >
      <span style={labelStyle} className={labelClassName}>
        {label}
      </span>
    </Link>
  );
};

export default NavItem;
